<!--评论组件-->
<template>
  <div>
    <div class="comment-box">
      <div v-if="isSignIn === 0" class="signInText"></div>
        <div class="bottom">
          <div class="dec">
              <span>共<span class="commentTotal">{{commentArray.length}}</span>条留言</span>
          </div>
          <div class="CommentInput">
              <img  :src="defaultAvatar"  alt="">
              <el-input
              type="textarea"
              :rows="3"
              placeholder="请输入内容并按回车键发送"
              v-model="from.createComment"
              class="inputbox"
              size="350"
              maxlength="350"
              resize="none"
              @keyup.13.native="SendComment()">
              </el-input>
          </div>
          <!-- 评论区 -->
          <ul class="Commentarea">
              <h3>热门留言</h3>
              <li :key="index" v-for="(item,index) in commentArray">
                  <div class="Commentareabox">
                      <div class="pic">
                          <img :src="item.avatar" alt="">
                      </div>
                      <div class="side">
                          <div class="CommentTitle">
                              <span class="nickname">{{item.nickname}}: </span>
                              <span class="cmcontent">{{item.createComment}}</span>
                          </div>
                          <div class="timerorlike">
                              <span class="timer">{{item.createTime}}</span>
                          </div>
                      </div>
                  </div>
              </li>
          </ul>
        </div>
    </div>
  </div>
</template>

<script>
import Cookie from 'js-cookie'
  export default {
    inject: ['reload'],
    props:['articleId'],
    data() {
      return {
        //评论数据
        commentArray:[
          {
            avatar: "https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png",//评论者头像
            createComment: "测试评论内容",//评论内容
            createTime: "2021-10-03 09:54:47",//评论时间
            id: 1,//评论id
            nickname: "路人",//评论者昵称
          }
        ],
        //用户信息
        UserInfo:{},
        //评论信息
        from:{
          createComment:'',//评论内容
          userId:'',//评论者id
          blogId:'',//文章id
        },

        defaultAvatar:require("@/assets/img/pl.jpg"),
      }
    },
    methods:{
        //去登录
      GotoLogin(){
        this.$router.push({name:'login'})
      },
      // 获取文章评论
      GetArticleComment(){
        if(this.articleId){
          this.$http.get('/comment/selectCommentBYyBlogID/'+this.articleId)
          .then(res => {
            this.commentArray=res.data.data.commentList
          })
        }
      },
      // 发送评论
      SendComment(){
        if(!this.UserInfo.noWords){
          if(JSON.parse(sessionStorage.getItem("user"))){
            // 发起评论请求
            this.from.userId=JSON.parse(sessionStorage.getItem("user")).id
            this.from.blogId=this.articleId
            console.log(this.from)
            this.$http.post('/comment/saveComment',this.from).then(res => {
              console.log(res.data.result)
              if(res.data.result){
                this.GetArticleComment()
                this.$message.success("评论成功")
                this.from.createComment='';
              }
            })
          }else {
            this.$message({
              message: '请登录后进行操作',
              type: 'warning',
              offset:80
            });
            this.$router.push({name:'login'})
          }
        }else {
          this.$message({
            message: '你以被禁言，暂时无法进行评论',
            type: 'warning',
            offset:80
          });
        }
      },
      //获取用户信息
      loadUserInfo(){
        const user=JSON.parse(sessionStorage.getItem("user"))
        if(user){
          this.UserInfo=user
          this.defaultAvatar=user.avatar
        }
      },
    },
    computed:{
      // 查询登录状态
      isSignIn(){
        return this.$store.state.isSignIn;
      },
    },
    created(){
      this.GetArticleComment();
      this.loadUserInfo()
    }
  }
</script>

